@import './vars.css';

a {
    text-decoration: none;
}

.container {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: var(--colour-light-bkg);
}

.index {
    width: 50%;
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 1.0rem;
}

.header {
    background-color: var(--colour-dark-bkg);
    color: var(--colour-dark-font);
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 2.0rem;
    padding: 0.5rem;
    box-sizing: border-box;
}

.footer {
    background-color: var(--colour-dark-bkg);
    color: var(--colour-dark-font);
    width: 100%;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: center;
    padding: 0.5rem;
    box-sizing: border-box;
}